/**
 *  form  Input 
 */
import React, { useState } from "react";
import { AutoComplete } from "antd";

/**
 * FormInput  props
 */
export interface HYAutoCompleteProps {
  /**  */
  autoFocus?: boolean;
  /**  */
  /**  */
  allowClear?: boolean;
  /**  */

  disabled?: boolean;
  /**  */
  labelColor?: string;
  /**  */
  note?: string;
  /**  */
  noteForError?: string;
  /**  */
  placeholder?: string;
  /**  */
  prefix?: React.ReactNode;
  /**  */
  realVal?: string;
  /**  */
  showVal?: string;
  /**  large: 40px/middle: 32px/small: 24px */
  size?: "large" | "middle" | "small";
  /**  */
  suffix?: React.ReactNode;
  /**  */
  title?: string;
  /**  */
  onChange?: (param: { realVal: string }) => void;
  /**  */
}

export const HYAutoComplete: React.FC<HYAutoCompleteProps> = (props) => {
  const { labelColor = "#A4A4A4", realVal, title, onChange } = props;
  return (
    <div>
      <div
        className="leading-10"
        style={{
          color: labelColor,
        }}
      >
        {title}
      </div>
      <AutoComplete
        value={realVal}
        onChange={(data) => {
          if (typeof onChange !== "function") return;
          onChange({ realVal: data });
        }}
      />
    </div>
  );
};
