import React, { useState } from "react";
import { BlurAway } from "./BlurAway";
// import { EXP } from "@hyc/hyclib-web-client";
import { CloseCircleOutlined } from "@ant-design/icons";
export type ValidateProps = {
  children?: React.ReactNode;
  onBlur?: (e: React.FocusEvent) => void;
  type?:
    | "naturalNumber"
    | "number"
    | "english"
    | "chinese"
    | "integer"
    | "numberOrEnglish"
    | "cellphone"
    | "email"
    | "idCard"
    | "postalCode"
    | "ip";
};
const validMsg = {
  naturalNumber: "",
  number: "",
  english: "",
  chinese: "",
  integer: "",
  numberOrEnglish: "",
  cellphone: "",
  email: "",
  idCard: "",
  postalCode: "",
  ip: "ip",
};
export const ValidateComp = (props) => {
  const [flag, setFlag] = useState(false);
  const validValue = () => {
    const { value, type } = props;
    if (!type || EXP.validateEXP(value, type)) {
      setFlag(false);
    } else {
      setFlag(true);
    }
  };
  return (
    <BlurAway
      onBlur={(e) => {
        validValue();
        props.onBlur?.(e);
      }}
    >
      {props.children}
      {flag ? (
        <>
          <CloseCircleOutlined
            style={{
              color: "#F1252D",
            }}
            className="mx-2"
          />
          <span className="text-red">{validMsg[props.type]}</span>
        </>
      ) : null}
    </BlurAway>
  );
};
