/**
 *  form  Input 
 */
import { InputNumber } from "antd";
import React, { useState } from "react";
import { ValidateComp } from "./ValidateComp";
import { Note } from "./NoteComp";

/**
 * FormInput  props
 */
export interface FormInputNumberpProps {
  /**  */
  autoFocus?: boolean;
  /**  */
  checkFixedRule?:
    | "naturalNumber"
    | "number"
    | "english"
    | "chinese"
    | "integer"
    | "numberOrEnglish"
    | "cellphone"
    | "email"
    | "idCard"
    | "postalCode"
    | "ip";
  /**  */
  decimalSeparator?: string;
  /**  */
  disabled?: boolean;
  /**  */
  formatter?: (value?: number | string) => string;
  /**  */
  max?: number;
  /**  */
  min?: number;
  /**  formatter  formatter  */
  parser?: (param: string | undefined) => number | string;
  /**  */
  precision?: number;
  /**  */
  readOnly?: boolean;
  /**  */
  size?: "large" | "middle" | "small";
  /**  */
  step?: number;
  /**  */
  title?: string;
  /**  */
  labelColor?: string;
  /**  */
  showVal?: number;
  /**  */
  placeholder?: string;
  /**  */
  note?: string;
  /**  */
  noteForError?: string;
  /**  */
  realVal?: number;
  /**  */
  onChange?: (param: { realVal: number | undefined | null }) => void;
  /**  */
  onBlur?: (param: {
    event: React.FocusEvent;
    realVal?: number;
    showVal?: number;
  }) => void;
  /**  */
  onFocus?: (param: {
    event: React.FocusEvent;
    realVal: number | undefined;
    showVal: number | undefined;
  }) => void;
  /**  */
  onClick?: (param: {
    event: React.MouseEvent;
    realVal: number | undefined;
    showVal: number | undefined;
  }) => void;
  /**  */
  onDbClick?: (param: {
    event: React.MouseEvent;
    realVal: number | undefined;
    showVal: number | undefined;
  }) => void;
  /**  */
  onPressEnter?: (param: {
    event: React.KeyboardEvent;
    realVal: number | undefined;
    showVal: number | undefined;
  }) => void;
  /**  */
  onStep?: (param: {
    realVal: number;
    info: { offset: number; type: "up" | "down" };
  }) => void;
}

export const FormInputNumber: React.FC<FormInputNumberpProps> = (props) => {
  const {
    autoFocus = false,
    checkFixedRule,
    decimalSeparator,
    disabled = false,
    formatter,
    min,
    max,
    parser,
    precision,
    readOnly = false,
    size = "middle",
    step,
    title,
    labelColor,
    note,
    placeholder,
    realVal,
    showVal,
    onChange,
    onBlur,
    onFocus,
    onClick,
    onDbClick,
    onStep,
  } = props;
  const [isFocus, setState] = useState(false);
  return (
    <div>
      {title ? (
        <div
          className="leading-10"
          style={{
            color: labelColor,
          }}
        >
          {title}
          <Note title={note} />
        </div>
      ) : null}

      <ValidateComp
        value={realVal}
        type={checkFixedRule}
        onBlur={(event) => {
          setState(false);
          onBlur?.({ realVal, showVal, event });
        }}
      >
        <InputNumber
          style={{ width: 300 }}
          disabled={disabled}
          autoFocus={autoFocus}
          decimalSeparator={decimalSeparator}
          formatter={formatter}
          min={min}
          max={max}
          parser={parser}
          precision={precision}
          readOnly={readOnly}
          size={size}
          step={step}
          placeholder={placeholder}
          value={realVal}
          onChange={(realValue) => {
            if (typeof onChange !== "function") return;
            onChange({
              realVal:
                typeof realValue === "string"
                  ? parseFloat(realValue)
                  : realValue,
            });
          }}
          onFocus={(event) => {
            setState(true);
            if (typeof onFocus !== "function") return;
            onFocus({ realVal, showVal, event });
          }}
          onClick={(event) => {
            if (typeof onClick !== "function") return;
            onClick({ realVal, showVal, event });
          }}
          onDoubleClick={(event) => {
            if (typeof onDbClick !== "function") return;
            onDbClick({ realVal, showVal, event });
          }}
          onStep={(realValue, info) => {
            if (typeof onStep !== "function") return;
            onStep({ realVal: realValue, info });
          }}
        />
      </ValidateComp>
    </div>
  );
};
