/**
 *  form  Input 
 */
import React, { useState } from "react";
import { Calendar } from "antd";
import moment from "moment";

/**
 * FormInput  props
 */
export interface HYCalendarProps {
  /**  */
  autoFocus?: boolean;
  /**  */
  addonAfter?: React.ReactNode;
  /**  */
  addonBefore?: React.ReactNode;
  /**  */
  allowClear?: boolean;
  /**  */
  bordered?: boolean;
  /**  */
  checkFixedRule?:
    | "naturalNumber"
    | "number"
    | "english"
    | "chinese"
    | "integer"
    | "numberOrEnglish"
    | "cellphone"
    | "email"
    | "idCard"
    | "postalCode"
    | "ip";
  /**  */
  disabled?: boolean;
  /**  */
  labelColor?: string;
  /**  */
  note?: string;
  /**  */
  noteForError?: string;
  /**  */
  placeholder?: string;
  /**  */
  prefix?: React.ReactNode;
  /**  */
  realVal?: moment.Moment;
  /**  */
  showVal?: string;
  /**  large: 40px/middle: 32px/small: 24px */
  size?: "large" | "middle" | "small";
  /**  */
  suffix?: React.ReactNode;
  /**  */
  title?: string;
  /**  */
  onChange?: (param: { realVal: moment.Moment }) => void;
  /**  */
}

export const HYCalendar: React.FC<HYCalendarProps> = (props) => {
  const { labelColor = "#A4A4A4", realVal, title, onChange } = props;
  return (
    <div>
      <div
        className="leading-10"
        style={{
          color: labelColor,
        }}
      >
        {title}
      </div>

      <Calendar
        value={realVal}
        onChange={(data) => {
          if (typeof onChange !== "function") return;
          onChange({ realVal: data });
        }}
      />
    </div>
  );
};
